import axios from 'axios'
/* eslint-disable */
const url = 'api/yacht-managements/'
let token = localStorage.getItem('jwt');
axios.defaults.headers.common = {'Authorization': `Bearer ${token}`}

class YachtManagementService {
  static getYachtManagementText () {
    return new Promise(function(resolve, reject) {
      axios.get("/api/yacht-managements/").then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }

  static updateYachtManagementText (id, text) {
    return new Promise(function(resolve, reject) {
      axios.put(`${url}${id}`, {
        text: text
      }).then(function(result) {
        resolve(result);
      }).catch(function(err) {
        reject(err);
      });
    });
  }
}

export default YachtManagementService